'use client';

import React, { useState } from 'react';
import Link from 'next/link';
import { motion, AnimatePresence } from 'framer-motion';
import { HiMenu, HiX, HiChevronDown } from 'react-icons/hi';
import * as DropdownMenu from '@radix-ui/react-dropdown-menu';

const navItems = [
  { name: 'Home', path: '/' },
  { name: 'About', path: '/about' },
  { name: 'Work', path: '/work' },
  { name: 'Blog', path: '/blog' },
];

const moreMenuItems = [
  { name: 'Projects', path: '/projects' },
  { name: 'Resume', path: '/resume' },
  { name: 'Testimonials', path: '/testimonials' },
];

export const Header: React.FC = () => {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  return (
    <>
      <motion.header
        className="fixed top-6 left-1/2 -translate-x-1/2 z-50 w-[96%] max-w-6xl"
        initial={{ y: -100, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ duration: 0.6, ease: "easeOut" }}
      >
        <div className="glass-nav rounded-full px-3 md:px-6 py-3">
          <div className="relative z-10 flex items-center justify-between gap-2">
            {/* Logo - Left */}
            <div className="shrink-0 min-w-[60px]">
              <Link href="/" className="text-lg md:text-xl font-bold hover:text-accent transition-colors group">
                <span className="text-foreground group-hover:glow-text-sm transition-all duration-300">Moeez R.</span>
              </Link>
            </div>

            {/* Desktop Navigation - Center */}
            <nav className="hidden md:flex items-center justify-center flex-1">
              <div className="flex items-center gap-1 lg:gap-2">
                {navItems.map((item) => (
                  <Link
                    key={item.path}
                    href={item.path}
                    className="text-sm text-secondary hover:text-foreground transition-colors font-medium px-3 py-1.5 rounded-full hover:bg-white/5"
                  >
                    {item.name}
                  </Link>
                ))}
              </div>
            </nav>

            {/* Right side - More menu and Book a Call */}
            <div className="hidden md:flex items-center gap-2 shrink-0">
              <DropdownMenu.Root>
                <DropdownMenu.Trigger asChild>
                  <button className="text-sm text-secondary hover:text-foreground transition-colors font-medium px-3 py-1.5 rounded-full hover:bg-white/5 flex items-center gap-1 outline-none">
                    More
                    <HiChevronDown className="text-xs" />
                  </button>
                </DropdownMenu.Trigger>

                <DropdownMenu.Portal>
                  <DropdownMenu.Content
                    className="min-w-40 glass-card rounded-2xl p-1.5 z-50 data-[state=open]:animate-in data-[state=closed]:animate-out data-[state=closed]:fade-out-0 data-[state=open]:fade-in-0 data-[state=closed]:zoom-out-95 data-[state=open]:zoom-in-95"
                    sideOffset={5}
                  >
                    {moreMenuItems.map((item) => (
                      <DropdownMenu.Item key={item.path} asChild>
                        <Link
                          href={item.path}
                          className="text-sm text-secondary hover:text-foreground transition-colors px-3 py-2.5 rounded-xl hover:bg-white/5 cursor-pointer flex items-center outline-none focus:bg-white/10 data-highlighted:bg-white/10"
                        >
                          {item.name}
                        </Link>
                      </DropdownMenu.Item>
                    ))}
                  </DropdownMenu.Content>
                </DropdownMenu.Portal>
              </DropdownMenu.Root>

              <Link
                href="/contact"
                className="text-sm bg-accent/10 hover:bg-accent/20 text-foreground px-5 py-2 rounded-full transition-all duration-300 border border-accent/30 hover:border-glow hover:glow-element"
              >
                Book a Call
              </Link>
            </div>

            {/* Mobile Menu Button - Right aligned on mobile */}
            <div className="md:hidden shrink-0">
              <button
                className="text-xl text-foreground hover:text-accent transition-colors p-2"
                onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
                aria-label="Toggle mobile menu"
              >
                {isMobileMenuOpen ? <HiX /> : <HiMenu />}
              </button>
            </div>
          </div>
        </div>
      </motion.header>

      {/* Mobile Navigation */}
      <AnimatePresence>
        {isMobileMenuOpen && (
          <motion.div
            className="fixed top-24 left-1/2 -translate-x-1/2 w-[90%] max-w-md z-40 md:hidden"
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            transition={{ duration: 0.2 }}
          >
            <div className="glass-card rounded-3xl p-6">
              <nav className="relative z-10 flex flex-col gap-4">
                {navItems.map((item) => (
                  <Link
                    key={item.path}
                    href={item.path}
                    className="text-secondary hover:text-accent transition-colors py-2 text-center"
                    onClick={() => setIsMobileMenuOpen(false)}
                  >
                    {item.name}
                  </Link>
                ))}
                <Link
                  href="/contact"
                  className="bg-accent hover:bg-accent/90 text-background px-6 py-3 rounded-full transition-all duration-300 text-center font-semibold mt-2 hover:glow-element-strong"
                  onClick={() => setIsMobileMenuOpen(false)}
                >
                  Book a Call
                </Link>
              </nav>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </>
  );
};
